import React, { useState } from 'react'
import './index.css';
import App from './App.js'
import App1 from './App1.js'
import qs from 'qs'
import PasteClient from "pastebin-api";





function Maincomp() {

    const client = new PasteClient("5AeqRvywRybXN9p0ETLJqWL_1i5Ff_w4");
    const [switchview, setSwitchview] = useState(true)

    
        
    const shareCode = async () => {
        const url = await client.createPaste({
            code: "const something = 'Hello World!'",
            expireDate: "N",
            format: "javascript",
            name: "something.js",
            publicity: 0,
          });

          console.log(url);
    }
    
    return switchview ? (
        <React.Fragment>
            
            <div className="center">
                <button style={{marginLeft:"10%"}} onClick={shareCode}>Share Code</button>
                <input type="checkbox" name="" value={switchview} onChange = {() => setSwitchview(value => !value)} />
            </div>
            <App1 />
        </React.Fragment>)
        :(
        <React.Fragment>
            <div className="center">
                <button style={{marginLeft:"10%"}}>Share Code</button>
                <input type="checkbox" name="" value={switchview} onChange = {() => setSwitchview(value => !value)} />
            </div>
            <App/>
        </React.Fragment>
    )

}

export default Maincomp
